html {
  font-family: 'Source Sans Pro';
  line-height: 1.5em;
}

.error {
  background-color: #d8b3b3;
  border-radius: 4px;
  border: 2px solid red;
  padding: 8px;
}

.success {
  background-color: #e1f4d8;
  border-radius: 4px;
  border: 2px solid green;
  padding: 8px;
}

.person {
  height: 32px;
}

input {
  margin-bottom: 10px;
}
button {
  padding: 4px;
}
